.single-movie-container {
  height: 80vh;
  width: auto;
  background-size: cover;
  background-blend-mode: hard-light;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie-information {
  border: 3px solid white;
  display: flex;
  justify-content: center;
  background-color: white;
  opacity: .9;
  height: 60vh;
  width: 100vw;
}

h2 {
  margin: 0;
}

.left-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 40vw;
  max-height: 60vh;
}

.movie-info-poster {
  width: 15em;
  height: auto;
}

.back-btn {
  color: black;
  font-size: 1.5em;

  border: 2px solid black;
  padding: 3px;
  background-color: rgb(191, 191, 191);
  border-radius: 5px;
}

p {
  margin: 10px;
}

.movie-overview {
  text-align: center;
}

.right-side {
  width: 40vw;
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
